<template>
  <div class="card">
    <div class="card__title">
      <b-row>
        <b-col style="max-width:max-content !important;">
          <span>Fleet Maintenance Schedule</span>
        </b-col>
        <b-col style="text-align: right;">
          <span>
            <ABSButton
              :text="'Back'"
              classButton="button button--back"
              classIcon="icon-style-1"
              @click="$router.go(-1)"
            />
          </span>
        </b-col>
      </b-row>
    </div>
    <div class="card__body">
      <div v-for="(list, index) in propLists">
        <ACCList2
          :prop="list.propList"
          :title="list.title"
          @rowClicked="rowClicked"
          @buttonDeleteClicked="doDeleteClick"
          @rowDblClicked="doDoubleClick"
          @rowLinkClick="rowLink"
          @pageSize="M_PageSize"
          @pagination="M_Pagination"
          @filter="M_Advance_Filter"
          @headTable="M_Head_Table"
          @refreshColumn="refreshColumn"
          ref="ref_list"
          @buttonViewClicked="doViewClick"
          ButtonBackDisabled
          :cTitleStyle="list.cTitleStyle"
        >
          <template slot="last_millage" slot-scope="data">
            <span>{{isCurrency(data.item.last_millage, 0)}}</span>
          </template>
          <template slot="current_millage" slot-scope="data">
            <span>{{isCurrency(data.item.current_millage, 0)}}</span>
          </template>
          <template slot="usage" slot-scope="data">
            <span>{{isCurrency(data.item.usage, 0)}}</span>
          </template>
        </ACCList2>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      search: "",
      propLists: [
        {
          title: 'Unscheduled',
          cTitleStyle: 'color: #CC0000 !important;',
          propList: {
            OptionUrl: "/OP/OP_FleetMaintSch",
            LineNo: 0,
            initialWhere: "ss_portfolio_id='" + this.getDataUser().portfolio_id + "' AND schedule_status = 'U'",
            OrderBy: "",
            SourceField: "",
            ParamView: "",
          },
        },
        {
          title: 'Scheduled',
          cTitleStyle: 'color: #9A0368 !important;',
          propList: {
            OptionUrl: "/OP/OP_FleetMaintSch",
            LineNo: 0,
            initialWhere: "ss_portfolio_id='" + this.getDataUser().portfolio_id + "' AND schedule_status = 'S'",
            OrderBy: "",
            SourceField: "",
            ParamView: "",
          },
        },
        {
          title: 'Queued Up',
          cTitleStyle: 'color: #333399 !important;',
          propList: {
            OptionUrl: "/OP/OP_FleetMaintSch",
            LineNo: 0,
            initialWhere: "ss_portfolio_id='" + this.getDataUser().portfolio_id + "' AND schedule_status = 'Q'",
            OrderBy: "",
            SourceField: "",
            ParamView: "",
          },
        },
        {
          title: 'On Progress',
          cTitleStyle: 'color: #00CC33 !important;',
          propList: {
            OptionUrl: "/OP/OP_FleetMaintSch",
            LineNo: 0,
            initialWhere: "ss_portfolio_id='" + this.getDataUser().portfolio_id + "' AND schedule_status = 'P'",
            OrderBy: "",
            SourceField: "",
            ParamView: "",
          },
        },
      ]
    };
  },
  computed: {
    ButtonStatus() {
      return this.$store.getters.getButtonStatus;
    },
  },
  methods: {
    rowClicked(record, index) {
      this.doViewClick(record, index);
    },
    doViewClick(record, index) {
      let param = record;
      param.isEdit = true;
      param.isView = true;
      this.$store.commit("setParamPage", param);
      this.$router.push({ name: "MM_FleetMaintenanceScheduleForm" });
    },
    doDoubleClick(record, index) {},
    doDeleteClick(record, index) {
      this.alertConfirmation("Are You Sure Want To Delete This Data ?").then(
        ress => {
          if (ress.value) {
            this.M_Delete(record, index);
          }
        }
      );
    },
    M_Delete(record, index) {
    },
    rowLink(url) {},
    M_PageSize() {},
    M_Pagination() {},
    M_Advance_Filter() {},
    M_Head_Table() {},
    refreshColumn() {},
    RenderData() {
      this.$nextTick(() => {
        this.$refs.ref_list[0].doGetList("");
        this.$refs.ref_list[1].doGetList("");
        this.$refs.ref_list[2].doGetList("");
        this.$refs.ref_list[3].doGetList("");
      });
    },
  },
  mounted() {
    this.RenderData();
  },
};
</script>